/* a form is a list of questions */
.questions {
  margin: 0;
  padding: 0;
  list-style: none; }

.questions li {
  clear: left;
  position: relative;
  padding: .5em 0 .7em;
  /* padding-bottom +.3em to balance padding-top (accounting for line-height on text) */
  list-style: none;
  -webkit-transition: opacity 0.2s ease, margin 0.2s ease, padding 0.2s ease;
  -o-transition: opacity 0.2s ease, margin 0.2s ease, padding 0.2s ease;
  transition: opacity 0.2s ease, margin 0.2s ease, padding 0.2s ease; }

form li[hidden="hidden"] * {
  display: none;
  max-height: 0; }

.questions li[hidden] {
  display: block;
  max-height: 0;
  opacity: 0;
  margin: 0 !important;
  padding: 0 !important;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  position: absolute; }

/* form footer */
form li.footer {
  list-style: none;
  padding-bottom: .5em;
  padding-top: 0.8em; }

.sections li.footer {
  padding: .5em; }

/* form actions */
.actions, #asides .actions {
  display: block;
  list-style: none;
  margin: 0;
  padding: .5em 0 0;
  overflow: hidden; }

.actions:before, .actions:after, #asides .actions:before, #asides .actions:after {
