/* compact question layout */
.group > fieldset .compact li {
  float: left;
  clear: none;
  margin-right: 1.5em; }

/* prevent floated selects from truncating */
.group select {
  max-width: 100%; }

/* https://github.com/h5bp/html5-boilerplate/wiki/css */
fieldset {
  border: none;
  min-width: 0;
  /* fix for iOS safari - fixes: elements withing fieldsets not scaling correctly */
  width: 100%;
  /* fix for iOS safari - fixes: elements withing fieldsets not scaling correctly */ }

legend {
  margin: 0;
  padding: 0;
  white-space: normal;
  width: 100%;
  border: 0;
  color: inherit; }

/* labels and alerts */
label {
  cursor: pointer; }

.label,
.hint,
label .help,
legend .help,
.alert {
  display: block; }

/* help inside a hint inline */
label .hint .help,
